p,
a,
span,
input,
button,
textarea,
ul,
li {
  font-family: "degular-display", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 18px;
  letter-spacing: 0.5px;
}

p {
  margin: 0;
}

button,
a {
  cursor: pointer;
}

:root {
  --border-radius: 3px;
  --guide: 14px;
  --main-color: #5833c3;
  --light-grey-background: #f1f1f1;
  --light-grey: #ddd;
}

.container {
  padding: 2rem;
}

.col {
  display: flex;
  flex-direction: column;
}

.col.settings {
  max-width: 300px;
  gap: 0.5rem;
}

.PromptInput {
  border-bottom: 1px solid var(--light-grey);
  display: flex;
  flex-direction: column;
}

.PromptInput textarea {
  border: 0;
  background-color: var(--light-grey-background);
  border-radius: 3px;
  resize: none;
  padding: 0.75rem;
  margin-bottom: 0.5rem;
}

.PromptInput textarea:focus-visible {
  outline: none;
}

span {
  font-size: 12px;
  margin-left: 1rem;
  cursor: pointer;
}

li {
  cursor: grab;
}

ul.PromptList {
  list-style: none;
  margin: 0;
  padding: 0;
  border-bottom: 1px solid var(--light-grey);
  padding-bottom: 0.3rem;
}

li.promptItem {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 6px 10px;
  background-color: var(--light-grey-background);
  justify-content: space-between;
  border-radius: 3px;
  margin-bottom: 0.2rem;
}

li.promptItem.editing {
  outline: 1px solid var(--main-color);
}

li.promptItem.editing input {
  width: 100%;
}

.editingPromptItem {
  border: 0;
  background: transparent;
}

.editingPromptItem:focus-visible {
  outline: none;
  padding: 0;
  margin: 0;
}

.PromptsListActions {
  display: flex;
  background-color: #f1f1f1;
  padding: 1rem 0.5rem;
  align-items: center;
  justify-content: center;
}

.generateBtn {
  display: flex;
}

.generateBtn button {
  border: none;
  background-color: var(--main-color);
  color: #fff;
  padding: 0.5rem 0;
  width: 100%;
  border-radius: var(--border-radius);
}

.smartPromp__modal {
  position: absolute;
  left: 350px;
  background-color: #fff;
  box-shadow: 0px 0px 4px 0px #ababab;
  padding: 1rem;
  border-radius: 3px;
  min-height: 50vh;
  width: 300px;
}

.modal_header {
  display: flex;
  justify-content: space-between;
}

.modal_header p {
  font-size: var(--guide);
  margin: 0;
}

.contextMenu {
  position: absolute;
  z-index: 100; /* Ensure it's above other elements */
  background-color: white;
  border: 1px solid #ddd;

  border-radius: 5px;
}

.contextMenu button {
  background: transparent;
  border: 0;
  color: red;
  transition: all 0.2s ease-in-out;
  font-size: 14px;
  margin: 0;
  padding: 6px 10px;
}

.contextMenu button:hover {
  background-color: red;
  color: #fff;
}
